@import "https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;700&display=swap";
:root {
  --accent-color: #e54065;
  --text-color: #636363;
  --border-color: #cfd2dc;
  --background-color: #fff;
  --read-background-color: #f2f2f2;
  --font-family: "Nunito", sans-serif;
  --filter-background-color: #e1e4ea;
  --body-background-color: #f4f5f9;
}

.email-card {
  box-sizing: border-box;
  background-color: var(--background-color);
  border: 1.5px solid var(--border-color);
  font-family: var(--font-family);
  color: var(--text-color);
  width: calc(100%-1rem);
  border-radius: 10px;
  flex-wrap: nowrap;
  margin: .7rem 1rem 1.25rem;
  padding: .75rem 1.5rem;
  transition: all .3s ease-in-out;
  display: flex;
}

.email-card.active {
  width: calc(35% - 1.5rem);
  flex-wrap: nowrap;
  margin: .7rem 1rem 1.25rem;
  padding: .75rem .75rem .75rem 1.5rem;
  display: flex;
  position: relative;
}

@media only screen and (max-width: 900px) {
  .email-card.active {
    display: none;
  }
}

.email-card.active.selected {
  border: 1.5px solid var(--accent-color);
  background-color: #fff;
}

.email-card__avatar {
  min-width: 2.5rem;
  height: 2.5rem;
  background-color: var(--accent-color);
  color: #fff;
  text-align: center;
  border-radius: 50%;
  align-self: flex-start;
  margin-right: 1.5rem;
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 2.5rem;
}

.email-card p {
  color: var(--text-color);
  margin: 0;
}

.email-card__content {
  overflow: hidden;
}

.email-card__subject {
  padding-top: 3px;
}

.email-card__description {
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 10px 0;
  overflow: hidden;
}

.email-card__date {
  font-size: 14px;
}

.read {
  background-color: var(--read-background-color);
}

.email-card__favorited {
  color: var(--accent-color);
  margin-left: auto;
  padding-left: 1.5rem;
  font-size: .9rem;
  font-weight: 700;
}

body {
  box-sizing: content-box;
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  background-color: var(--body-background-color);
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

.container {
  margin: 0;
  padding: 1rem;
  overflow: hidden;
}

.filter {
  font-family: var(--font-family);
  color: var(--text-color);
  align-items: center;
  margin: 0 1rem .5rem;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
}

.filter p {
  margin-right: .5em;
  font-size: .9rem;
}

.filter-button {
  background-color: #0000;
  border: none;
  border-radius: 20px;
  margin-right: .5rem;
  padding: .4rem 1rem;
  font-size: .9rem;
  transition: all .2s ease-in-out;
}

.filter-button.selected {
  color: var(--text-color);
  border: 1px solid var(--border-color);
  background-color: #e1e5ea;
}

.filter-button:hover {
  background-color: var(--border-color);
  cursor: pointer;
}

.email-list {
  min-height: 80vh;
  height: 100%;
  flex-wrap: wrap;
  align-content: flex-start;
  position: relative;
}

@media only screen and (max-width: 900px) {
  .filter.active {
    display: none;
  }

  .email-list {
    height: 100%;
  }

  .filter-button:hover {
    background-color: #e1e5ea;
  }
}

.menu {
  display: none;
}

.menu__button {
  border: 1.5px solid var(--border-color);
  color: var(--accent-color);
  width: 11ch;
  background-color: #0000;
  border-radius: 5px;
  padding: .5rem 1rem;
  font-size: 1rem;
  transition: background-color .2s ease-in-out;
}

.menu__button:hover {
  background-color: var(--accent-color);
  color: #fff;
  cursor: pointer;
}

.menu.active {
  display: none;
}

@media only screen and (max-width: 900px) {
  .menu.active {
    font-family: var(--font-family);
    color: var(--text-color);
    align-items: center;
    margin-bottom: 1rem;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
  }
}

.pagination-container {
  justify-content: center;
  align-items: center;
  margin-left: .5rem;
  display: flex;
}

.pagination-button {
  border: 1.5px solid var(--border-color);
  color: var(--accent-color);
  width: 11ch;
  background-color: #0000;
  border-radius: 5px;
  margin: 0 .5rem;
  padding: .5rem 1rem;
  font-size: 1rem;
  transition: background-color .2s ease-in-out;
}

.pagination-button:hover:enabled {
  background-color: var(--accent-color);
  color: #fff;
  cursor: pointer;
}

.pagination-button:disabled {
  color: var(--text-color);
  cursor: default;
}

.pagination-button.active {
  background-color: var(--accent-color);
  color: #fff;
}

.email-body {
  box-sizing: border-box;
  font-family: var(--font-family);
  color: var(--text-color);
  width: 0;
  height: 1px;
  background-color: var(--background-color);
  border: 1.5px solid var(--border-color);
  visibility: hidden;
  border-radius: 10px;
  flex-direction: column;
  padding: 30px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  right: 1rem;
  transform: translateX(100%);
}

.email-body.active {
  width: calc(65% - 1.5rem);
  height: max-content;
  visibility: visible;
  margin-left: 1rem;
  transform: translateX(0);
}

@media only screen and (max-width: 900px) {
  .email-body.active {
    width: 100%;
    margin: 0 auto;
    padding: 1rem;
    right: 0;
  }
}

.email-body__header {
  align-items: flex-start;
  margin-bottom: .5rem;
  display: flex;
}

.email-body__avatar {
  width: 2.5rem;
  min-width: 2.5rem;
  height: 2.5rem;
  background-color: var(--accent-color);
  color: #fff;
  text-align: center;
  border-radius: 50%;
  margin-right: 1.5rem;
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 2.5rem;
}

.email-body__date {
  margin: 0 0 1rem;
  padding-left: 4rem;
  font-size: small;
  line-height: 1.5;
}

.email-body__subject {
  margin: 0;
  padding: 0;
  font-size: 1.75rem;
  font-weight: 700;
}

.email-body__body {
  padding-left: 4rem;
  font-size: medium;
  line-height: 1.5;
  overflow: hidden;
}

.email-body__favorite-button {
  background-color: var(--accent-color);
  color: #fff;
  width: 20ch;
  cursor: pointer;
  border: none;
  border-radius: 20px;
  margin-left: auto;
  padding: 10px 20px;
  font-size: small;
}

.email-body__favorite-button.favorited {
  border: 1px solid var(--accent-color);
  color: var(--accent-color);
  background-color: #fff;
}

